import React from 'react';
import PropTypes from 'prop-types';

import orderService from 'services/orderService';
import localStorageHelper from 'helpers/localStorageHelper';
import Icon from 'components/common/Icon/icon';

import preloader from 'styles/icons/preloader.svg';
import './ordersPage.scss';

export default class OrdersPage extends React.PureComponent {
    static propTypes = {
        history: PropTypes.shape({
            length: PropTypes.number.isRequired,
            action: PropTypes.string.isRequired,
            location: PropTypes.shape({
                pathname: PropTypes.string.isRequired,
                search: PropTypes.string.isRequired,
                hash: PropTypes.string.isRequired,
                key: PropTypes.string.isRequired
            }),
            push: PropTypes.func.isRequired
        }).isRequired
    }

    constructor(props) {
        super(props);
        this.state = {
            orders: [],
            isLoading: true
        };
    }

    componentDidMount = async () => {
        const result = localStorageHelper.getRole() === 'admin'
            ? await orderService.getPending()
            : await orderService.getByUserId(localStotageHelper.getId());
        if (result) {
            this.setState({
                orders: result,
                isLoading: false
            });
        }
    }

    render() {
        if (localStorageHelper.getId() === '' || localStorageHelper.getId() === null) {
            this.props.history.push('/login');
            return null;
        }

        if (localStorageHelper.getRole() === 'admin') {
            return (
                <div className="orders-page">
                    <h1 className="orders-page__title">
                        Orders waiting for approval
                    </h1>
                </div>
            );
        }

        return (
            <div className="orders-page">
                <h1 className="orders-page__title">
                    Your orders
                </h1>
            </div>
        );
    }
}
